.favorites-img {
  height: 526px;
  width: 335px;
  background-image: url("favorite-mob.7a729a02.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.food-type-list {
  overflow: overlay;
  gap: 12px;
  margin-bottom: 24px;
  display: flex;
}

.food-type-list .btn {
  color: rgba(5, 5, 5, .5);
  white-space: nowrap;
  background-color: #fff;
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  padding: 14px 18px;
  font-size: 18px;
}

.btn:hover, .btn:focus, .btn:active {
  color: #fff;
  background-color: #9bb537;
  border-color: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .favorites-img {
    height: 356px;
    width: 704px;
    background-image: url("favorite-tab.fe44eaad.jpg");
    margin-bottom: 64px;
  }

  .favorites-img .food-type-list {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .favorites-img {
    width: 1080px;
    background-image: url("favorite-pc.40c7059d.jpg");
  }
}

